.App-link {
  color: #09d3ac;
}

.weather-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}

.weather-item {
  flex: 1 100%;
  /* flex: 1 0 0; */
  background: hsl(220, 13%, 15%);
  padding: 5px;
  margin: 0.2em;

  box-shadow: 0 1px 3px hsla(0, 0%, 0%, 12%), 0 1px 2px hsla(0, 0%, 0%, 24%);

  line-height: 2em;
  color: hsl(220, 13%, 95%);
  font-weight: bold;
  font-size: 1em;
  text-align: center;

  border-radius: 0.5em;
}

.weather-item-empty {
  background: repeating-linear-gradient(
    45deg,
    white,
    white 10px,
    hsl(220, 13%, 95%) 10px,
    hsl(220, 13%, 95%) 20px
  );
}

.time {
  display: inline-block;
  margin-right: 0.5em;
  line-height: 1.8em;
  vertical-align: text-bottom;
}

.forecast-date {
  margin-top: 1em;
}

.temperature {
  white-space: nowrap;
}

.temperature-number {
  font-size: 1.8em;
}
.temperature-degree {
  font-size: 1em;
  font-weight: normal;
  position: relative;
  display: inline-block;
  top: -0.7em;
}

@media all and (min-width: 410px) {
  .weather-item {
    flex: 1 0 0;
  }
}
